import React, { useState, useEffect } from "react";
import axios from "axios";
import { Link } from "react-router-dom";
export function Home() {
  const [pokemons, setPokemons] = useState([]);
  useEffect(() => {
    handleFetch();
  }, []);
  const handleFetch = async () => {
    axios
      .get("https://pokeapi.co/api/v2/pokemon/?limit=764")
      .then(resp => setPokemons(resp.data.results));
  };
  return (
    <div className="grid">
      {!pokemons
        ? "Loading..."
        : pokemons.map((pokemon, index) => (
            <div key={index} className="card shadow p-2">
              <h6 className="py-2">{pokemon.name}</h6>
              <img
                src={`https://raw.githubusercontent.com/PokeAPI/sprites/master/sprites/pokemon/${index +
                  1}.png`}
                alt={pokemon.name}
                width="100px"
                height="100px"
                className="mx-auto"
              />

              <Link to={`/poke/${index + 1}`}>
                <h6 className="btn btn-sm btn-primary">info</h6>
              </Link>
            </div>
          ))}
    </div>
  );
}
