import React, { useState, useEffect } from "react";
import axios from "axios";
import { Link, Route } from "react-router-dom";

export function Pokemon({ match }) {


  const [pokemon, setPokemon] = useState({
    name: "",
    abilities: [],
    sprites: [],
    types: [],
    moves: []
  });
  const url = `https://pokeapi.co/api/v2/pokemon/${match.params.id}`;
  useEffect(() => {
    handlePokemon();
  });
  const handlePokemon = async () => {
    axios.get(url).then(resp => setPokemon(resp.data));
  };
  return (
    <div className="container text-center">
      <div className="card shadow p-2">
        <h5 className="my-2">{pokemon.name}</h5>

        <img
          src={pokemon.sprites.front_default}
          alt={pokemon.name}
          className="mx-auto"
          width="150px"
          height="150px"
        />

        <h5>
          <u>Abilities</u>
        </h5>
        <div className="flex-wrap mx-auto mb-2">
          {pokemon.abilities.map((ab, ind) => (
            <h6 key={ind} className="text-dark">
              <span className="badge badge-pill badge-warning px-3 py-1 mx-2">
                {ab.ability.name}
              </span>
            </h6>
          ))}
        </div>

        <h5>
          <u>Type</u>
        </h5>

        <div className="flex mx-auto mb-2">
          {pokemon.types.map((type, ind) => (
            <span
              key={ind}
              className="badge badge-pill badge-success mx-2 px-3 py-1"
            >
              {type.type.name}
            </span>
          ))}
        </div>

        <h5>
          <u>Moves</u>
        </h5>
        <div className="flex-wrap mx-auto mb-2">
          {pokemon.moves.map((move, ind) => (
            <span
              key={ind}
              className="badge badge-pill badge-danger m-1 px-3 py-1"
            >
              {move.move.name}
            </span>
          ))}
        </div>
        <Link to="/">
          <h6 className="mb-4 mt-3 btn btn-primary btn-sm">home</h6>
        </Link>
      </div>
      
    </div>
  );
}
